import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Week } from '../shared/model/sk/week';
import { StimmungskalenderService } from '../services/stimmungskalender.service';

@Component({
  selector: 'app-note-dialog',
  templateUrl: './note-dialog.component.html',
  styleUrls: ['./note-dialog.component.scss'],
})
export class NoteDialogComponent {
  note: string;

  constructor(
    private skService: StimmungskalenderService,
    public dialogRef: MatDialogRef<NoteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Week
  ) {
    this.note = data.note;
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  saveNote(): void {
    this.dialogRef.close();
    this.skService
      .saveNote({
        week_date: this.data.week_date,
        note: this.note,
      } as Week)
      .subscribe({
        complete: () => void 0, // completeHandler
        error: () => void 0, // errorHandler
        next: () => void 0, // nextHandler
      });
  }
}
