import {
  BreakpointObserver,
  Breakpoints,
  BreakpointState,
} from '@angular/cdk/layout';
import { HttpErrorResponse } from '@angular/common/http';
import { Component, Input, OnInit } from '@angular/core';
import { catchError, map, Observable, of } from 'rxjs';

import { MessageService } from 'src/app/services/message.service';
import { SettingsService } from 'src/app/services/settings.service';
import { StimmungskalenderService } from 'src/app/services/stimmungskalender.service';
import { ErrorMsg } from 'src/app/shared/error-msg';
import { Messages } from 'src/app/shared/messages';
import { InputEntry } from 'src/app/shared/model/sk/input-entry';
import { WeekDayEntry } from 'src/app/shared/model/sk/week-day-entry';
import { Moods } from '../../shared/model/sk/moods';
@Component({
  selector: 'app-mood-entry',
  templateUrl: './mood-entry.component.html',
  styleUrls: ['./mood-entry.component.scss'],
})
export class MoodEntryComponent implements OnInit {
  @Input() entry: WeekDayEntry = <WeekDayEntry>{};
  @Input() form = '';
  moods = Moods;
  selectedVal = '';
  buttonsDisabled = false;
  error: ErrorMsg | null = null;
  isMobile$ = new Observable<boolean>();
  colorsEnabled: Observable<boolean>;

  constructor(
    private skService: StimmungskalenderService,
    private msgService: MessageService,
    private breakpointObserver: BreakpointObserver,
    private settingsService: SettingsService
  ) {
    this.colorsEnabled = this.settingsService.colorsEnabled;
  }

  ngOnInit(): void {
    if (this.form === 'night') {
      this.selectedVal = String(this.entry.mood_night);
    } else if (this.form === 'day') {
      this.selectedVal = String(this.entry.mood_day);
    }

    this.checkMobile();
  }

  checkMobile(): void {
    this.isMobile$ = this.breakpointObserver
      .observe([
        Breakpoints.XSmall,
        Breakpoints.Small,
        Breakpoints.Medium,
        Breakpoints.Large,
        Breakpoints.XLarge,
        Breakpoints.Handset,
        Breakpoints.Tablet,
        Breakpoints.Web,
        Breakpoints.HandsetPortrait,
        Breakpoints.TabletPortrait,
        Breakpoints.WebPortrait,
        Breakpoints.HandsetLandscape,
        Breakpoints.TabletLandscape,
        Breakpoints.WebLandscape,
      ])
      .pipe(
        map((breakpoint) => {
          if (
            breakpoint.breakpoints[Breakpoints.XSmall] ||
            breakpoint.breakpoints[Breakpoints.Small]
          ) {
            return true;
          } else {
            return false;
          }
        })
      );
  }

  getCoverCssClass(): Observable<string> {
    return this.breakpointObserver
      .observe([
        Breakpoints.XSmall,
        Breakpoints.Small,
        Breakpoints.Medium,
        Breakpoints.Large,
        Breakpoints.XLarge,
      ])
      .pipe(
        map((breakpoint: BreakpointState) => {
          if (breakpoint.breakpoints[Breakpoints.XSmall]) {
            return 'cover-xsmall';
          }
          if (breakpoint.breakpoints[Breakpoints.Small]) {
            return 'cover-small';
          }
          if (breakpoint.breakpoints[Breakpoints.Medium]) {
            return 'cover-medium';
          }
          if (breakpoint.breakpoints[Breakpoints.Large]) {
            return 'cover-large';
          }
          if (breakpoint.breakpoints[Breakpoints.XLarge]) {
            return 'cover-xlarge';
          }
          return 'cover-small';
        })
      );
  }

  onButtonToggleClick(mood: string, day: string): void {
    this.buttonsDisabled = true;
    if (mood === this.selectedVal) {
      this.selectedVal = '';
    } else {
      this.selectedVal = mood;
    }
    this.skService
      .saveEntry({
        mood: +mood,
        period: this.form,
        day: day,
      } as InputEntry)
      .pipe(
        catchError((err: HttpErrorResponse) => {
          this.error = {
            title: 'Got an error while setting mood',
            detail: err.message,
          } as ErrorMsg;
          return of(<WeekDayEntry>{});
        })
      )
      .subscribe(() => {
        this.buttonsDisabled = false;
        this.msgService.sendMessage(Messages.RELOAD);
      });
  }
}
