import { Pipe, PipeTransform } from '@angular/core';
import { addWeeks, parse } from 'date-fns';

@Pipe({
  name: 'strToWeekEndDate',
})
export class StrToWeekEndDatePipe implements PipeTransform {
  transform(value: string): Date {
    return addWeeks(parse(value, 'yyyy-MM-dd', new Date()), 1);
  }
}
