import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import {
  ApexChart,
  ApexNonAxisChartSeries,
  ApexResponsive,
  ApexTheme,
  ChartComponent,
} from 'ng-apexcharts';
import { map, Observable } from 'rxjs';
import { StimmungskalenderService } from 'src/app/services/stimmungskalender.service';
import { Moods } from 'src/app/shared/model/sk/moods';
import { Periods } from 'src/app/shared/periods';

export type ChartOptions = {
  series: ApexNonAxisChartSeries;
  chart: ApexChart;
  responsive: ApexResponsive[];
  labels: string[];
  theme: ApexTheme;
};

@Component({
  selector: 'app-pie-chart',
  templateUrl: './pie-chart.component.html',
  styleUrls: ['./pie-chart.component.scss'],
})
export class PieChartComponent implements OnInit {
  @Input() period = '';
  @ViewChild('chart') chart: ChartComponent = <ChartComponent>{};
  chartOptions$ = new Observable<ChartOptions>();
  header = '';

  constructor(
    private skService: StimmungskalenderService,
    private translocoService: TranslocoService
  ) {}

  ngOnInit(): void {
    this.chartOptions$ = this.skService.getPieChartGraph(this.period).pipe(
      map((data) => {
        const chartOptions = this.defaultGraph();
        chartOptions.series = data.values;
        const labels: string[] = [];
        data.label_numbers.forEach((val) =>
          labels.push(
            this.translocoService.translate(Moods[+val].toLowerCase())
          )
        );
        chartOptions.labels = labels;
        return chartOptions;
      })
    );

    this.setHeader();
  }

  private setHeader() {
    if (this.period === Periods.DAY) {
      this.header = this.translocoService.translate('day_moods');
    } else if (this.period === Periods.NIGHT) {
      this.header = this.translocoService.translate('night_moods');
    }
  }

  private defaultGraph(): ChartOptions {
    return {
      series: [],
      chart: {
        width: 380,
        type: 'pie',
      },
      labels: [],
      responsive: [
        {
          breakpoint: 480,
          options: {
            chart: {
              width: 200,
            },
            legend: {
              position: 'bottom',
            },
          },
        },
      ],
      theme: {
        mode: 'dark',
      },
    } as ChartOptions;
  }
}
