import { HttpErrorResponse } from '@angular/common/http';
import { Component, HostListener, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { addWeeks, format, parse, subWeeks } from 'date-fns';
import { catchError, Observable, of, tap } from 'rxjs';
import { NoteDialogComponent } from '../note-dialog/note-dialog.component';
import { MessageService } from '../services/message.service';
import { StimmungskalenderService } from '../services/stimmungskalender.service';
import { ErrorMsg } from '../shared/error-msg';
import { Forms } from '../shared/forms';
import { Messages } from '../shared/messages';
import { MoodTable } from '../shared/model/sk/mood-table';
import { Moods } from '../shared/model/sk/moods';
import { Week } from '../shared/model/sk/week';
import { QP_START_DAY } from '../shared/query-params';
import { StandoutData } from './../shared/model/sk/standout-data';

const SK_DATE_FORMAT = 'yyyy-MM-dd';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
})
export class HomeComponent implements OnInit {
  moodTable$ = new Observable<MoodTable>();
  standoutData$ = new Observable<StandoutData[]>();
  moods = Moods;
  forms = Forms;
  error: ErrorMsg | null = null;
  loading = true;
  currentWeek = new Date();

  constructor(
    private skService: StimmungskalenderService,
    private dialog: MatDialog,
    private msgService: MessageService,
    private route: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.standoutData$ = this.skService.getStandoutData();

    this.buildMsgListener();
    this.route.queryParams.subscribe((params: Params) => {
      if (params.hasOwnProperty(QP_START_DAY)) {
        this.currentWeek = parse(
          <string>params[QP_START_DAY],
          SK_DATE_FORMAT,
          new Date()
        );
      } else {
        this.currentWeek = new Date();
      }
      this.loadEntries();
    });
  }

  @HostListener('document:keydown', ['$event'])
  handleKeyDown($event: KeyboardEvent): void {
    const inputElement = $event.target as HTMLInputElement;

    /* We ignore keys coming from input fields */
    if (
      inputElement.tagName === 'MAT-SLIDER' ||
      inputElement.tagName === 'INPUT' ||
      inputElement.tagName === 'TEXTAREA'
    ) {
      return;
    }

    /* We don't want to interfere with non-ampd-shortcuts like Alt+Tab or Ctrl+R */
    if ($event.ctrlKey || $event.metaKey) {
      return;
    }

    /* We don't want to interfere with tab changes */
    if ($event.altKey) {
      return;
    }

    switch ($event.key) {
      case 'a': // Open note dialog
        this.openNoteDialog();
        break;
      case '<':
      case 'ArrowLeft': // Left: Previous track
        this.prevWeek();
        break;
      case '>':
      case 'ArrowRight': // Right: Next track
        this.nextWeek();
        break;
      default:
        // Ignore it
        return;
    }

    $event.preventDefault();
  }

  openNoteDialog(): void {
    this.moodTable$.subscribe((moodTable) => {
      const dialogRef = this.dialog.open(NoteDialogComponent, {
        width: '75%',
        data: {
          week_date: moodTable.week.week_date,
          note: moodTable.week.note,
        } as Week,
      });
      dialogRef
        .afterClosed()
        .subscribe(() => this.msgService.sendMessage(Messages.RELOAD));
    });
  }

  nextWeek(): void {
    this.currentWeek = addWeeks(this.currentWeek, 1);
    void this.router
      .navigate([], {
        queryParams: {
          [QP_START_DAY]: format(this.currentWeek, SK_DATE_FORMAT),
        },
      })
      .then(() => this.msgService.sendMessage(Messages.RELOAD));
  }

  prevWeek(): void {
    this.currentWeek = subWeeks(this.currentWeek, 1);
    void this.router
      .navigate([], {
        queryParams: {
          [QP_START_DAY]: format(this.currentWeek, SK_DATE_FORMAT),
        },
      })
      .then(() => this.msgService.sendMessage(Messages.RELOAD));
  }

  private loadEntries(): void {
    this.moodTable$ = this.skService
      .getMoodTable(format(this.currentWeek, SK_DATE_FORMAT))
      .pipe(
        catchError((err: HttpErrorResponse) => {
          this.error = {
            title: 'Got an error while fetching entries',
            detail: err.message,
          } as ErrorMsg;
          return of(<MoodTable>{});
        }),
        tap(() => (this.loading = false))
      );
  }

  private buildMsgListener(): void {
    this.msgService.getMessages().subscribe((msg) => {
      switch (msg) {
        case Messages.RELOAD:
          this.loadEntries();
          break;
        default:
          // Ignore it
          return;
      }
    });
  }
}
