import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map, Observable, switchMap } from 'rxjs';
import {
  ENTRY_DATE_URL,
  MOOD_TABLE_URL,
  SAVE_NOTE_URL,
  SCATTER_GRAPH_URL,
  SEARCH_URL,
} from '../shared/api-urls';
import { ScatterGraphResponse } from '../shared/model/scatter-graph-response';
import { InputEntry } from '../shared/model/sk/input-entry';
import { MoodTable } from '../shared/model/sk/mood-table';
import { StandoutData } from '../shared/model/sk/standout-data';
import { Week } from '../shared/model/sk/week';
import { WeekDayEntry } from '../shared/model/sk/week-day-entry';
import { SearchForm } from '../shared/search-form';
import { PIE_CHART_GRAPH_URL, STANDOUT_DATA_URL } from './../shared/api-urls';
import { PieChartGraphResponse } from './../shared/model/pie-chart-graph-response';
import { AuthService } from './auth.service';

@Injectable({
  providedIn: 'root',
})
export class StimmungskalenderService {
  constructor(private authService: AuthService, private http: HttpClient) {}

  getMoodTable(week = ''): Observable<MoodTable> {
    week = week.trim();
    const options = week
      ? { params: new HttpParams().set('start_day', week) }
      : {};

    return this.authService.currentUser.pipe(
      switchMap(() => {
        return this.http.get<MoodTable>(MOOD_TABLE_URL, options);
      })
    );
  }

  getStandoutData(): Observable<StandoutData[]> {
    return this.http
      .get<StandoutData[]>(STANDOUT_DATA_URL)
      .pipe(map((data) => data.filter((entry) => entry.entry !== null)));
  }

  getScatterGraph(): Observable<ScatterGraphResponse[]> {
    return this.http.get<ScatterGraphResponse[]>(SCATTER_GRAPH_URL);
  }

  getPieChartGraph(period: string): Observable<PieChartGraphResponse> {
    return this.http.get<PieChartGraphResponse>(
      `${PIE_CHART_GRAPH_URL}?period=${period}`
    );
  }

  saveEntry(entry: InputEntry): Observable<WeekDayEntry> {
    return this.http.post<WeekDayEntry>(ENTRY_DATE_URL, entry);
  }

  saveNote(note: Week): Observable<Week> {
    return this.http.post<Week>(SAVE_NOTE_URL, note);
  }

  search(searchForm: SearchForm): Observable<Week[]> {
    let params = new HttpParams();
    params = params.set('start_day', searchForm.start);
    params = params.set('end_day', searchForm.end);
    params = params.set('mood', searchForm.mood);
    params = params.set('search_term', searchForm.searchTerm);
    return this.authService.currentUser.pipe(
      switchMap(() => {
        return this.http.get<Week[]>(SEARCH_URL, {
          params: params,
        });
      })
    );
  }
}
