import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Component } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { TranslocoService } from '@ngneat/transloco';
import { SettingsService } from '../services/settings.service';
import { SET_LANGUAGE_URL } from '../shared/api-urls';
import { AVAILABLE_LANGUAGES } from './../shared/language';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.scss'],
})
export class SettingsComponent {
  selectedLanguage: string;
  languages = AVAILABLE_LANGUAGES;
  colorsEnabled: Observable<boolean>;

  constructor(
    private http: HttpClient,
    private translocoService: TranslocoService,
    private snackBar: MatSnackBar,
    private settingsService: SettingsService
  ) {
    this.selectedLanguage = translocoService.getActiveLang();
    this.colorsEnabled = this.settingsService.colorsEnabled;
  }

  onLanguageSelectionChange(language: string): void {
    this.http
      .post<void>(SET_LANGUAGE_URL, {
        language: language,
      })
      .subscribe(() =>
        this.snackBar.open('Settings saved', 'Close', {
          duration: 3 * 1000,
        })
      );

    this.translocoService.setActiveLang(language);
  }

  onColorsEnabledChange(enabled: boolean): void {
    this.settingsService.setColorsEnabled(enabled);
  }
}
