import { Component, OnInit, ViewChild } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import {
  ApexChart,
  ApexDataLabels,
  ApexGrid,
  ApexAxisChartSeries,
  ApexStroke,
  ApexTheme,
  ApexTitleSubtitle,
  ApexTooltip,
  ApexXAxis,
  ApexYAxis,
  ChartComponent,
} from 'ng-apexcharts';
import { Observable, map } from 'rxjs';
import { StimmungskalenderService } from 'src/app/services/stimmungskalender.service';
import { Moods } from 'src/app/shared/model/sk/moods';

export type ChartOptions = {
  // annotations: ApexAnnotations;
  chart: ApexChart;
  colors: string[];
  dataLabels: ApexDataLabels;
  // fill: ApexFill;
  grid: ApexGrid;
  // labels: string[];
  // legend: ApexLegend;
  // plotOptions: ApexPlotOptions;
  // responsive: ApexResponsive[];
  series: ApexAxisChartSeries;
  // states: ApexStates;
  stroke: ApexStroke;
  // subtitle: ApexTitleSubtitle;
  theme: ApexTheme;
  title: ApexTitleSubtitle;
  tooltip: ApexTooltip;
  xaxis: ApexXAxis;
  yaxis: ApexYAxis | ApexYAxis[];
};

@Component({
  selector: 'app-scatter-plot',
  templateUrl: './scatter-plot.component.html',
  styleUrls: ['./scatter-plot.component.scss'],
})
export class ScatterPlotComponent implements OnInit {
  @ViewChild('chart') chart: ChartComponent = <ChartComponent>{};
  chartOptions$ = new Observable<ChartOptions>();

  constructor(
    private skService: StimmungskalenderService,
    private translocoService: TranslocoService
  ) {}

  ngOnInit(): void {
    this.chartOptions$ = this.skService.getScatterGraph().pipe(
      map((data) => {
        const chartOptions = this.defaultGraph();
        chartOptions.xaxis.categories = data.map((i) => i.x);
        chartOptions.series[0].name = this.translocoService.translate('day');
        chartOptions.series[0].data = data.map((i) => i.y.day);
        chartOptions.series[1].name = this.translocoService.translate('night');
        chartOptions.series[1].data = data.map((i) => i.y.night);
        return chartOptions;
      })
    );
  }

  private defaultGraph(): ChartOptions {
    return {
      series: [
        {
          name: '',
          data: [],
        },
        {
          name: '',
          data: [],
        },
      ],
      chart: {
        height: 350,
        type: 'line',
        zoom: {
          enabled: false,
        },
      },
      colors: ['#546E7A', '#E91E63'],
      dataLabels: {
        enabled: false,
      },
      stroke: {
        curve: 'straight',
      },
      theme: {
        mode: 'dark',
      },
      title: {
        text: this.translocoService.translate('scatterplot'),
        align: 'left',
      },
      tooltip: {
        enabled: true,
      },
      grid: {
        row: {
          colors: [], // takes an array which will be repeated on columns
          opacity: 1,
        },
      },
      xaxis: {
        categories: [],
      },
      yaxis: {
        labels: {
          formatter: (value) => {
            return this.translocoService.translate(Moods[+value].toLowerCase());
          },
        },
      },
    } as ChartOptions;
  }
}
