import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { catchError, map, mergeMap, of, shareReplay } from 'rxjs';
import { BehaviorSubject } from 'rxjs/internal/BehaviorSubject';
import { Observable } from 'rxjs/internal/Observable';
import { LOGIN_URL, LOGOUT_URL, VERIFY_URL } from '../shared/api-urls';
import { BackendUser } from '../shared/model/auth/backend-user';
import { LoginResponse } from '../shared/model/auth/login-response';
import { User } from '../shared/model/auth/user';

export const LS_USER = 'NG_SK_USER';

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  public currentUser = new Observable<User>();
  private currentUserSubject: BehaviorSubject<User>;

  constructor(private http: HttpClient, private router: Router) {
    const currentUser = localStorage.getItem(LS_USER) || '';
    try {
      this.currentUserSubject = new BehaviorSubject<User>(
        <User>JSON.parse(currentUser)
      );
    } catch (err) {
      this.currentUserSubject = new BehaviorSubject<User>(<User>{});
    }
    this.currentUser = this.currentUserSubject.asObservable();
  }

  login(username: string, password: string): Observable<User> {
    return this.http
      .post<LoginResponse>(LOGIN_URL, {
        username,
        password,
      })
      .pipe(
        map((response) => {
          const currentUser: User = response.user;
          currentUser.token = response.access_token;
          localStorage.setItem(LS_USER, JSON.stringify(currentUser));
          this.currentUserSubject.next(currentUser);
          return currentUser;
        })
      );
  }

  isLoggedIn(): Observable<boolean> {
    return this.currentUser.pipe(map((user) => JSON.stringify(user) !== '{}'));
  }

  _isLoggedIn(): Observable<boolean> {
    return this.currentUser.pipe(
      mergeMap((user) => {
        if (user.token) {
          return this.http
            .post<BackendUser>(VERIFY_URL, {
              token: user.token,
            })
            .pipe(
              map(() => true),
              catchError(() => of(false))
            );
        } else {
          return of(false);
        }
      })
    );
  }

  logout(): void {
    this.currentUser.pipe(shareReplay(1)).subscribe((user) => {
      this.http
        .post<void>(LOGOUT_URL, { token: user.token })
        .subscribe(() => void 0);
    });
    this.currentUserSubject.next(<User>{});
    localStorage.removeItem(LS_USER);
    void this.router.navigate(['/login']);
  }
}
