import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { DateRange } from '@angular/material/datepicker';
import { PageEvent } from '@angular/material/paginator';
import { ActivatedRoute, Router } from '@angular/router';
import { format } from 'date-fns';
import { Observable } from 'rxjs';
import { StimmungskalenderService } from '../services/stimmungskalender.service';
import { Moods } from '../shared/model/sk/moods';
import { Week } from '../shared/model/sk/week';
import { SearchForm } from '../shared/search-form';
import { PAGE_SIZE_OPTIONS, PAGINATION_TO } from './../shared/pagination';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss'],
})
export class SearchComponent implements OnInit {
  range = new FormGroup({
    start: new FormControl(),
    end: new FormControl(),
  });
  weeks$ = new Observable<Week[]>();
  selectedMood = 0;
  moods = Moods;
  pageSizeOptions = PAGE_SIZE_OPTIONS;
  paginationFrom = 0;
  paginationTo = PAGINATION_TO;

  constructor(
    private skService: StimmungskalenderService,
    private router: Router,
    private route: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.route.queryParamMap.subscribe((queryParams) => {
      const start = <string>queryParams.get('start_day') || '';
      const end = <string>queryParams.get('end_day') || '';
      const mood = +(queryParams.get('mood') || 0);
      const searchTerm = <string>queryParams.get('term') || '';

      const sf = {
        start: start,
        end: end,
        mood: mood,
        searchTerm: searchTerm,
      } as SearchForm;
      this.weeks$ = this.skService.search(sf);
    });

    this.range.valueChanges.subscribe((event: DateRange<Date>) => {
      if (event.start) {
        void this.router.navigate(['search'], {
          queryParams: { start_day: format(event.start, 'yyyy-MM-dd') },
          queryParamsHandling: 'merge',
        });
      }
      if (event.end) {
        void this.router.navigate(['search'], {
          queryParams: { end_day: format(event.end, 'yyyy-MM-dd') },
          queryParamsHandling: 'merge',
        });
      }
    });
  }

  onSelectionChange(mood: number): void {
    void this.router.navigate(['search'], {
      queryParams: { mood: mood },
      queryParamsHandling: 'merge',
    });
  }

  getPaginatorData(event: PageEvent): PageEvent {
    this.paginationFrom = event.pageIndex * event.pageSize;
    this.paginationTo = this.paginationFrom + event.pageSize;
    return event;
  }
}
